const formula = `
<pre><code>if (row + 1 > col) {
    table[row][col] = table[row - 1][col];
} else {
    let min = floors;
    for (let f = 1; f <= col; f += 1) {
        const max = Math.max(
            table[row - 1][f - 1],
            table[row][col - f]
        );
        min = Math.min(min, max);
    }
    table[row][col] = 1 + min;
}
</code></pre>
`;

const introduction =
  "Objective:  There are n number of eggs and building which has k floors. Write an algorithm to find the minimum number of drops is required to know the floor from which if egg is dropped, it will break.";

const note = `<ul>
<dl>One trial is  dropping an egg once from the particular floor. </dl>
<dl>If egg does not break after dropping, will be used again. </dl>
<dl>If egg breaks when dropped from some floor then it will break if dropped from any higher floor. </dl>
<dl>If egg does not break when dropped from some floor then it will not break if dropped from any lower floor</dl>
</ul>.
`;

export const modalBody = `
<b>Introduction: </b>
</br>
${introduction}
</br>
<b>Note: </b></br>
${note}
<b>Formula: </b>
</br>
${formula}`;



