import { connect } from "react-redux";

import Table from "presentational/Table";
import Buttons from "presentational/Buttons";
import Dashboard from "presentational/Dashboard";
import Modal from "presentational/Modal";
import Header from "presentational/Header";

import { buttonClick, closeModal, openModal, refreshData } from "./actions";

export const createTable = () => {
  const mapStateToProps = (state, ownProps) => ({
    ...state,
    table: state.longestIncreasingSubsequenceReducer.table,
    styles: state.longestIncreasingSubsequenceReducer.styles
  });
  return connect(mapStateToProps, {})(Table);
};

export const createDashboard = () => {
  const mapStateToProps = (state, ownProps) => ({
    ...state,
    score: state.longestIncreasingSubsequenceReducer.score,
    errors: state.longestIncreasingSubsequenceReducer.errors,
    steps: state.longestIncreasingSubsequenceReducer.steps
  });
  const mapDispatchToProps = dispatch => ({
    onClick: () => {
      dispatch(refreshData());
    }
  });
  return connect(mapStateToProps, mapDispatchToProps)(Dashboard);
};

export const createButtons = () => {
  const mapStateToProps = state => ({
    ...state,
    buttons: state.longestIncreasingSubsequenceReducer.buttons
  });
  const mapDispatchToProps = dispatch => ({
    onClick: buttonValue => {
      dispatch(buttonClick(buttonValue));
    }
  });
  return connect(mapStateToProps, mapDispatchToProps)(Buttons);
};

export const createModal = () => {
  const mapStateToProps = state => ({
    ...state,
    showModal: state.longestIncreasingSubsequenceReducer.showModal,
    closeModal: state.longestIncreasingSubsequenceReducer.closeModal,
    modalTitle: state.longestIncreasingSubsequenceReducer.modalTitle,
    modalBody: state.longestIncreasingSubsequenceReducer.modalBody
  });
  const mapDispatchToProps = dispatch => ({
    closeModal: () => {
      dispatch(closeModal());
    }
  });
  return connect(mapStateToProps, mapDispatchToProps)(Modal);
};

export const createHeader = () => {
  const mapStateToProps = state => ({
    ...state,
    title: state.longestIncreasingSubsequenceReducer.title,
    openModal: state.longestIncreasingSubsequenceReducer.openModal,
    count: state.longestIncreasingSubsequenceReducer.count
  });
  const mapDispatchToProps = dispatch => {
    dispatch({ type: "GET_LONGEST_INCREASING_SUBSEQUENCE_COUNT" });
    return { openModal: () => dispatch(openModal()) };
  };
  return connect(mapStateToProps, mapDispatchToProps)(Header);
};



